<template>
  <div class="container py-5">   
   
      <h3 v-if ="id == ''"><i class="fas fa-users"></i> Registro de proveedores</h3>
      <h3 v-else><i class="fas fa-users"></i> Edici√≥n de proveedor {{this.form.nombreComercial}}</h3>

    <hr>
        <b-form @submit="onSubmit" autocomplete="off">
          <b-row>
            <b-col class="col-md-6 col-sm-12 col-12">
              <label class="mt-3" for="cboRegimenFiscal">Regimen Fiscal <span class="text-danger"> *</span></label>
              <b-form-select
                    v-model="form.regimen"                      
                    id="cboRegimenFiscal"
                    required
                    >  
                    <b-form-select-option disabled :value="null">Seleccione regimen fiscal</b-form-select-option>
                    <b-form-select-option value="Moral">Persona Moral</b-form-select-option>
                    <b-form-select-option value="Fisica">Persona F√≠sica</b-form-select-option>                         
              </b-form-select>                
            </b-col>
          </b-row>

          <b-row v-if ="form.regimen == 'Fisica'">
            <b-col class='col-md-4 col-sm-12 col-12'>
              <label class="mt-3" for="nombre">Nombre<span class="text-danger"> *</span></label> 
                <b-form-input
                id="nombre"
                v-model="form.nombre"
                required
                >
                </b-form-input>
            </b-col>

            <b-col class='col-md-4 col-sm-12 col-12'>
              <label class="mt-3" for="ApellidoP">Apellido Paterno<span class="text-danger"> *</span></label> 
                <b-form-input
                id="ApellidoP"
                v-model="form.apellidoP"
                required
                >
                </b-form-input>
            </b-col>

            <b-col class='col-md-4 col-sm-12 col-12'>
              <label class="mt-3" for="ApellidoM">Apellido Materno<span class="text-danger"> *</span></label> 
                <b-form-input
                id="ApellidoM"
                v-model="form.apellidoM"
                required
                >
                </b-form-input>
            </b-col>

            <b-col class="col-md-4 col-sm-12 col-12">
              <div class="row mt-4 ml-4 mb-4">
                <div class="custom-control custom-switch">
                  <input                                
                      type="checkbox"
                      class="custom-control-input"
                      id="rfcGenerico"
                      v-model="form.rfcGenerico"                                
                  />
                  <label class="custom-control-label mt-3" for="rfcGenerico">RFC GEN√âRICO</label>
               </div>
              </div>
            </b-col>

            <b-col class="col-md-4 col-sm-12 col-12">                
              <label class="mt-3" for="txtRFC">RFC <span class="text-danger"> *</span></label>
               <b-form-input
                    id="txtRFC"
                    type="text"
                    v-model="form.rFC"
                    :disabled="form.rfcGenerico"
                    required
                    placeholder="RFC..."
                    class="text-uppercase"
                    :state="form.rFCValidate.esValido"
                >
                </b-form-input>
            </b-col>      

            <b-col class="col-md-4 col-sm-12 col-12">
              <label class="mt-3" for="nombreComercial">Nombre Comercial<span class="text-danger"> *</span></label> 
                <b-form-input
                id="nombreComercial"
                v-model="form.nombreComercial"
                required
                >
                </b-form-input>
            </b-col>
 
            <b-col class="col-md-4 col-sm-12 col-12">
              <label class="mt-3" for="cP">Codigo Postal<span class="text-danger"> *</span></label> 
               <b-form-input
                id="cP"
                v-model="form.cP"
                required
                >
                </b-form-input>
            </b-col>
       
            <b-col class="col-md-4 col-sm-12 col-12">
              <label class="mt-3" for="estado">Estado<span class="text-danger"> *</span></label> 
               <b-form-input
                id="estado"
                v-model="form.estado"
                required
                >
                </b-form-input>
            </b-col>
              
            <b-col class="col-md-4 col-sm-12 col-12">
              <label class="mt-3" for="municipio">Municipio<span class="text-danger"> *</span></label> 
                <b-form-input
                id="municipio"
                v-model="form.municipio"
                required
                >
                </b-form-input>
            </b-col>
                
            <b-col class="col-md-4 col-sm-12 col-12">
              <label class="mt-3" for="ciudad">Ciudad<span class="text-danger"> *</span></label> 
                <b-form-input
                id="ciudad"
                v-model="form.ciudad"
                required
                >
                </b-form-input>
              </b-col>
  
              <b-col class="col-md-4 col-sm-12 col-12">
                <label  class="mt-3" for="colonia">Colonia<span class="text-danger"> *</span></label> 
                  <b-form-select
                  id="colonia"
                  v-model="form.idColonia"
                  :options="colonias" 
                  required
                  >
                  </b-form-select>
              </b-col>
                  

              <b-col class="col-md-4 col-sm-12 col-12">
                <label  class="mt-3" for="calleNumero">Calle y N√∫mero<span class="text-danger"> *</span></label> 
                  <b-form-input
                  id="calleNumero"
                  v-model="form.calleNumero"
                  required
                  >
                  </b-form-input>
              </b-col>
          
              <b-col class="col-md-4 col-sm-12 col-12">
                <label  class="mt-3" for="tipo">Tipo<span class="text-danger"> *</span></label> 
                <b-form-select v-model="form.tipo" id="tipo" required>
                  <b-form-select-option disabled :value="null">Seleccione un tipo...</b-form-select-option>
                  <b-form-select-option value="Servicios">Servicios</b-form-select-option>
                  <b-form-select-option value="Gastos">Gastos</b-form-select-option>
                  <b-form-select-option value="Materiales">Materiales</b-form-select-option>
                </b-form-select>
              </b-col>
    
              <b-col class="col-md-4 col-sm-12 col-12">
                <label  class="mt-3" for="txtuser">Usuario<span class="text-danger"> *</span></label> 
                  <b-form-input
                  id="txtuser"
                  v-model="form.usuario"
                  required
                  >
                  </b-form-input>
              </b-col>
        
              <b-col class="col-md-4 col-sm-12 col-12">
                <label  class="mt-3" for="txtpassword">Contrase√±a<span class="text-danger"> *</span></label> 
                   <vue-password
                    id="txtpassword"
                    v-model="form.contrase√±a"
                    required
                    type="password"
                    disableStrength="false"
                    autocomplete="new-password"
                    />
              </b-col>

              <b-col class="col-md-6 col-sm-12 col-12" align-self="end"></b-col>
              <b-col class="col-md-3 col-sm-12 col-12 mt-3" align-self="end">
                <b-button
                  type="submit"
                  block
                  variant="success"
                  class="mr-3 mt-3"
                >
                  <i class="fa fa-save"/> Guardar
                </b-button>
              </b-col>
              <b-col class="col-md-3 col-sm-12 col-12 mt-3" align-self="end">
                <b-button
                  @click="cancelar"
                  block
                  variant="danger"
                  class="mr-3 mt-3"
                >
                  Cancelar
                </b-button>
              </b-col>
          </b-row>


          <b-row v-if ="form.regimen == 'Moral'">
  
            <b-col class="col-md-4 col-sm-12 col-12">
              <label class="mt-3" for="nombreComercial">Nombre Comercial <span class="text-danger"> *</span></label> 
                <b-form-input
                id="nombreComercial"
                v-model="form.nombreComercial"
                required
                >
                </b-form-input>
            </b-col>
            <b-col class="col-md-4 col-sm-12 col-12">                
                <!-- <b-form-group id="input-group-4" label="RFC *" label-for="txtRFC"> -->
                <label class="mt-3" for="txtRFC">RFC <span class="text-danger"> *</span></label>
               
                <b-form-input
                    id="txtRFC"
                    type="text"
                    v-model="form.rFC"
                    placeholder="RFC..."
                    class="text-uppercase"
                    required
                    :state="form.rFCValidate.esValido"
                >
                </b-form-input>
                <!-- </b-form-group> -->
            </b-col>

            <b-col class="col-md-4 col-sm-12 col-12">
              <label class="mt-3" for="tipo">Tipo <span class="text-danger"> *</span></label> 
             
              <b-form-select v-model="form.tipo" id="tipo" required>
                <b-form-select-option disabled :value="null">Seleccione un tipo...</b-form-select-option>
                <b-form-select-option value="Servicios">Servicios</b-form-select-option>
                <b-form-select-option value="Gastos">Gastos</b-form-select-option>
                <b-form-select-option value="Materiales">Materiales</b-form-select-option>
              </b-form-select>
              </b-col>
              
              <b-col class="col-md-4 col-sm-12 col-12">
                <label class="mt-3" for="txtuser">Usuario <span class="text-danger"> *</span></label> 
                  <b-form-input
                  id="txtuser"
                  v-model="form.usuario"
                  required
                  >
                  </b-form-input>
              </b-col>
                  
              <b-col class="col-md-4 col-sm-12 col-12">
                <label class="mt-3" for="txtpassword">Contrase√±a<span class="text-danger"> *</span></label> 
                  
                  <vue-password
                    id="txtpassword"
                    v-model="form.contrase√±a"
                    required
                    type="password"
                    disableStrength="false"
                    autocomplete="new-password"
                    />
              </b-col>
               
              <b-col class="col-md-6 col-sm-12 col-12" align-self="end"></b-col>
              <b-col class="col-md-3 col-sm-12 col-12 mt-3" align-self="end">
                <b-button
                  type="submit"
                  block
                  variant="success"
                  class="mr-2 mt-2"
                >
                  <i class="fa fa-save"></i> Guardar
                </b-button>
              </b-col>
              <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
                <b-button
                  @click="cancelar"
                  block
                  variant="danger"
                  class="mr-2 mt-2"
                >
                  Cancelar
                </b-button>
              </b-col>
          </b-row>
           </b-form>

  </div>
</template>
<script>
import servicio from "@/services/servicio-proveedores";
import servicioGenerales from "@/services/servicio-general";
import servicioSeguridad from '@/services/servicio-seguridad'
export default {
  data() {
    return {
        fieldTypes:{
          password:'password'
        },

        contacts:[
            'Nombre',
            'Puesto',
            'Telefeno',
            'Correo'
        ],
    //se deja un espacio en blanco para el boton
    fieldsContactos: ['','Nombre', 'Tel√©fono', 'Telefono M√≥vil','Correo Electr√≥nico'],        
    currentLocation:{
        lat:0,
        lng:0
      },
      activeGeneral:false,
      activeDirecciones: false,
      activeCredito:false,
      activeContactos:false,
      disabledVendedor: false,
      permisoCobranza:false,
      mostrarDirecciones:false,
      mostrarDireccionFiscal:false,
      mostrarContactos:true,
      optionsRS: [
        { value: null, text: "Seleccione Regimen Fiscal..." },
        { value: 'Moral', text: "Persona Moral" },
        { value: 'Fisica', text: "Persona F√≠sica" },
      ],
      form: {
        regimen: null,
        nombre: "",
        apellidoP: "",
        apellidoM: "",
        nombreComercial: "",
        rfcGenerico: false,
        rFC: "",
        cP: "",
        estado: "",
        municipio: "",
        ciudad: "",
        idColonia: null,
        calleNumero: "",
        tipo: null,
        usuario: "",
        contrase√±a: null,
        rFCValidate:{
          esValido: false,
          msg:""
        }
      },
      vendedores: [{ value: null, text: "Seleccione un vendedor..." }],
      //FORMULARIO DIRECCION
      bancos: [{ value:null, disabled: true, text: "seleccione un banco"}],
      colonias: [{ value: null, disabled: true, text: "Seleccione una colonia..." }],
      regimen: [{ value: null, disabled: true, text: "Seleccione regimen fiscal..." }],
      //tiposObras: [{ value: null, text: "Seleccione el tipo de obra..." }],
      disabledSubtipos: true,
      disabledMunicipio: true,
      disabledColonia: true,
      disabledCP: false,      
      //FINALIZA FORM DIRECCION
      //TABLA DE OBRAS
      rutaNuevo: "TiposConcreto/Registro",
      permisoEscritura: true,
      itemsDireccionesObras: [],      
      itemsDireccionesFiscales: [],
      itemsContactos: [],
      fieldsObras: [
        {
          key: "colonia",
          label: "Colonia",
          sortable: true,
          class: "text-center",
        },
        {
          key: "domicilio",
          label: "Domicilio",
          sortable: true,
          sortDirection: "desc",
        },
        { key: "apiGoogleMaps", label: "Accion" },
        // { key: 'show_details', label: 'Detalles' }
      ],      
      //finaliza tabla de obras
      //TABLA DE DIRECCIONES
        fieldsDireccionesFiscales: [
        {
            key: "colonia",
            label: "Colonia",
            sortable: true,
            class: "text-center",
        },
        {
            key: "domicilio",
            label: "Domicilio",
            sortable: true,
            sortDirection: "desc",
        },
        { key: "apiGoogleMaps", label: "Accion" },
            // { key: 'show_details', label: 'Detalles' }
        ],      

      //finaliza tabla de direcciones
    };
  },
  
  async mounted() {
      this.activeGeneral = true;      
    // if (this.verificaTokenValido()) {
      this.options = null;
      this.id = this.$route.params.id != undefined ? this.$route.params.id : 0;      
      this.cargaProveedor();    
    //   this.consultaTiposObras();
      this.permisoEscritura = true;
      this.$loading(true);
        await servicioSeguridad.verificaPermiso(this.usuario.idUsuario,"cobranza").then(r=>{
          this.permisoCobranza = r.data;
        });
      this.$loading(false);

      // this.permisoEscritura = this.verificaPermiso("clientes");
    // }
  },
  computed: {
    
  },
  
  
  watch: {
    "form.cP": function(newValue, oldValue){
        if(newValue != oldValue){
            
            if(newValue.length == 5){                
                this.obtenerDatosCP();
            }else{
                this.colonias = [{ value: null,disabled: true, text: "Seleccione una colonia..." }];                                              
                this.form.municipio = "";
            }
        }
    },
    "form.rFC": function (newValue) {
      if (this.form.rFC != undefined) {
        this.form.rFC = this.form.rFC.toUpperCase();
        if (
          /^([A-Z,√ë,&]{3,4}([0-9]{2})(0[1-9]|1[0-2])(0[1-9]|1[0-9]|2[0-9]|3[0-1])[A-Z|\d]{3})$/.test(
            newValue
          )
        ) {
          this.form.rFCValidate.esValido = true;
          this.form.rFCValidate.msg = "El RFC es v√°lido";
        } else {
          this.form.rFCValidate.esValido = false;
          this.form.rFCValidate.msg = "El RFC NO es v√°lido";
        }
      }else {
				this.form.rFCValidate.esValido = null;
				this.form.rFCValidate.msg = "";
			}
    },
    "form.rfcGenerico":function (newValue){
        this.form.rFC = newValue ? 'XAXX010101000' : '';
    }
  },
  methods: {      
    onSubmit(evt) {
      evt.preventDefault();
      this.guardarProveedor();
      
    },
   
   
    guardarProveedor() {
      let proveedorPost
      if (this.form.regimen == "Fisica"){
        proveedorPost = {
          regimen: this.form.regimen,
          idProveedor: this.id,
          nombre: this.form.nombre,
          apellidoP: this.form.apellidoP,
          apellidoM: this.form.apellidoM,
          nombreComercial: this.form.nombreComercial,
          rFC: this.form.rFC,
          cP: this.form.cP,
          estado: this.form.estado,
          municipio: this.form.municipio,
          ciudad: this.form.ciudad,
          idColonia: this.form.idColonia,
          calleNumero: this.form.calleNumero,
          tipo: this.form.tipo,
          usuario: this.form.usuario,
          contrase√±a: this.form.contrase√±a,     

        };
      }else{
          proveedorPost = {
            regimen: this.form.regimen,
            idProveedor: this.id,
            nombreComercial: this.form.nombreComercial,
            rFC: this.form.rFC,
            tipo: this.form.tipo,
            usuario: this.form.usuario,
            contrase√±a: this.form.contrase√±a,     
          };
      }
      
      servicio.guardarProveedor(proveedorPost).then((r) => {
      console.log(r.value)
      if (r.value) {
         
            // alert(codUltimoCliente);                             
            this.$swal.fire({            
                text: `El proveedor ${this.form.nombreComercial} se ha guardado exitosamente!`,
                icon: "success",
            });
          
         window.history.back();

        } else {
          this.$swal.fire({
            text: `No se pudo guardar el proveedor, contacte a sistemas`,
            icon: "error",
          });
        }
      });
    },
    cargaProveedor() {
      if (this.id > 0) {
        this.$loading(true);

        servicio.proveedores(this.id).then((r) => {
          if (r.value) {
            console.log(r);
            this.form.regimen = r.data.proveedor[0]?.regimen;
            this.form.nombre = r.data.proveedor[0]?.nombre;
            this.form.apellidoP = r.data.proveedor[0]?.apellidoP;
            this.form.apellidoM = r.data.proveedor[0]?.apellidoM;
            this.form.nombreComercial = r.data.proveedor[0]?.nombreComercial;
            this.form.rFC = r.data.proveedor[0]?.rFC;
            this.form.cP = r.data.proveedor[0]?.cP;
            this.form.estado = r.data.proveedor[0]?.estado;
            this.form.municipio = r.data.proveedor[0]?.municipio;
            this.form.ciudad = r.data.proveedor[0]?.ciudad;
            this.form.idColonia = r.data.proveedor[0]?.idColonia;
            this.form.calleNumero = r.data.proveedor[0]?.calleNumero;
            this.form.tipo = r.data.proveedor[0]?.tipo;
            this.form.usuario = r.data.proveedor[0]?.usuario;
            this.form.contrase√±a = r.data.proveedor[0]?.contrase√±a;
                        
          } else {
            this.$loading(false);
            this.$swal.fire({
              text: `${r.message}`,
              icon: "error",
            });
            setTimeout(() => {
              this.$router.push({ path: "." });
            }, 1000);
          }
        });
      }
    },


obtenerDatosCP() {
      this.$loading(true);
          servicioGenerales.obtenerDatosCP(this.form.cP).then((r) => {
            console.log(r)
            if (r.value) {
              r.data.forEach((x) => {
                this.colonias.push({ value: x.codigoColonia, text: x.colonia });
                this.$loading(false);
              });
              this.form.estado = r.data[0].estado;
              this.form.municipio = r.data[0].municipio;
              this.form.ciudad = r.data[0].ciudad;
              
            //   this.form.direccionObraciudad = this.form.direccionObracodEstado == 10 ?  'Tamazula': this.form.direccionObraciudad;
              this.disabledCP = true;
            } else {
              this.$loading(false);
              this.$swal.fire({
                text: r.message,
                icon: "warning",
              });
            }
          });
    },
    
    cancelar() {
      let tipoActividad =
        this.id == 0
          ? "¬øDesea cancelar el registro de un nuevo proveedor?"
          : "¬øDesea cancelar la actualizaci√≥n del proveedor?";
      this.$swal
        .fire({
          title: tipoActividad,
          text: "",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Si, volver al cat√°logo",
          cancelButtonText: "Cancelar",
        })
        .then((result) => {
          if (result.value) {
            window.history.back();
          }
        });
    },
  },
};
</script>
<style scoped>
.active{
    color: black !important
}
</style>